___CSS_LOADER_EXPORT___.push([module.id, "\n.vue-star-rating-star[data-v-e9e0e2aa] {\n    overflow: visible !important;\n}\n", "",{"version":3,"sources":["webpack://src/star.vue"],"names":[],"mappings":";AAkQA;IACA,4BAAA;AACA","sourcesContent":["<template>\n  <svg\n    class=\"vue-star-rating-star\"\n    :height=\"starSize\"\n    :width=\"starSize\"\n    :viewBox=\"viewBox\"\n    @mousemove=\"mouseMoving\"\n    @click=\"selected\"\n  >\n\n    <linearGradient\n      :id=\"grad\"\n      x1=\"0\"\n      x2=\"100%\"\n      y1=\"0\"\n      y2=\"0\"\n    >\n      <stop\n        :offset=\"starFill\"\n        :stop-color=\"(rtl) ? getColor(inactiveColor) : getColor(activeColor)\"\n        :stop-opacity=\"(rtl) ? getOpacity(inactiveColor) : getOpacity(activeColor)\"\n      />\n      <stop\n        :offset=\"starFill\"\n        :stop-color=\"(rtl) ? getColor(activeColor) : getColor(inactiveColor)\"\n        :stop-opacity=\"(rtl) ? getOpacity(activeColor) : getOpacity(inactiveColor)\"\n      />\n    </linearGradient>\n\n    <filter\n      :id=\"glowId\"\n      height=\"130%\"\n      width=\"130%\"\n      filterUnits=\"userSpaceOnUse\"\n    >\n      <feGaussianBlur\n        :stdDeviation=\"glow\"\n        result=\"coloredBlur\"\n      />\n      <feMerge>\n        <feMergeNode in=\"coloredBlur\" />\n        <feMergeNode in=\"SourceGraphic\" />\n      </feMerge>\n    </filter>\n\n    <polygon\n      v-if=\"glowColor\"\n      v-show=\"fill > 1\"\n      :points=\"starPointsToString\"\n      :fill=\"gradId\"\n      :stroke=\"glowColor\"\n      :filter=\"'url(#'+glowId+')'\"\n    />\n\n    <polygon\n      :points=\"starPointsToString\"\n      :fill=\"gradId\"\n      :stroke=\"getBorderColor\"\n      :stroke-width=\"border\"\n      :stroke-linejoin=\"roundedCorners ? 'round' : 'miter'\"\n    />\n    <polygon\n      :points=\"starPointsToString\"\n      :fill=\"gradId\"\n    />\n  </svg>\n</template>\n\n<script type=\"text/javascript\">\nexport default {\n    name: 'Star',\n    props: {\n        fill: {\n            type: Number,\n            default: 0\n        },\n        points: {\n            type: Array,\n            default() {\n                return []\n            }\n        },\n        size: {\n            type: Number,\n            default: 50\n        },\n        starId: {\n            type: Number,\n            required: true\n        },\n        activeColor: {\n            type: String,\n            required: true\n        },\n        inactiveColor: {\n            type: String,\n            required: true\n        },\n        borderColor: {\n            type: String,\n            default: '#000'\n        },\n        borderWidth: {\n            type: Number,\n            default: 0\n        },\n        roundedCorners: {\n            type: Boolean,\n            default: false\n        },\n        rtl: {\n            type: Boolean,\n            default: false\n        },\n        glow: {\n            type: Number,\n            default: 0\n        },\n        glowColor: {\n            type: String,\n            default: \"transparent\",\n            required: false\n        }\n    },\n    data() {\n        return {\n            starPoints: [19.8, 2.2, 6.6, 43.56, 39.6, 17.16, 0, 17.16, 33, 43.56],\n            grad: '',\n            glowId: ''\n        }\n    },\n    computed: {\n        starPointsToString() {\n            return this.starPoints.join(',')\n        },\n        gradId() {\n            return 'url(#' + this.grad + ')'\n        },\n        starSize() {\n            // Adjust star size when rounded corners are set with no border, to account for the 'hidden' border\n            const size = (this.roundedCorners && this.borderWidth <= 0) ? parseInt(this.size) - parseInt(this.border) : this.size\n            return parseInt(size) + parseInt(this.border)\n        },\n        starFill() {\n            return (this.rtl) ? 100 - this.fill + '%' : this.fill + '%'\n        },\n        border() {\n            return (this.roundedCorners && this.borderWidth <= 0) ? 6 : this.borderWidth\n        },\n        getBorderColor() {\n            if (this.roundedCorners && this.borderWidth <= 0) {\n                // create a hidden border\n                return (this.fill <= 0) ? this.inactiveColor : this.activeColor\n            }\n\n            return this.borderColor\n        },\n        maxSize() {\n            return this.starPoints.reduce(function(a, b) {\n                return Math.max(a, b)\n            })\n        },\n        viewBox() {\n            return '0 0 ' + this.maxSize + ' ' + this.maxSize\n        }\n    },\n    created() {\n        this.starPoints = (this.points.length) ? this.points : this.starPoints\n        this.calculatePoints()\n        this.grad = this.getRandomId()\n        this.glowId = this.getRandomId()\n    },\n    methods: {\n        mouseMoving($event) {\n            this.$emit('star-mouse-move', {\n                event: $event,\n                position: this.getPosition($event),\n                id: this.starId\n            })\n        },\n        getPosition($event) {\n            // calculate position in percentage.\n            var starWidth = (92 / 100) * this.size\n            const offset = (this.rtl) ? Math.min($event.offsetX, 45) : Math.max($event.offsetX, 1)\n            var position = Math.round((100 / starWidth) * offset)\n\n            return Math.min(position, 100)\n        },\n        selected($event) {\n            this.$emit('star-selected', {\n                id: this.starId,\n                position: this.getPosition($event)\n            })\n        },\n        getRandomId() {\n            return Math.random().toString(36).substring(7)\n        },\n        calculatePoints() {\n            this.starPoints = this.starPoints.map((point) => {\n                return ((this.size / this.maxSize) * point) + (this.border * 1.5)\n            })\n        },\n        parseAlphaColor(inputColor) {\n            const patterns = [\n                {\n                    // rgba\n                    pattern: /^rgba\\((\\d{1,3}%?\\s*,\\s*){3}(\\d*(?:\\.\\d+)?)\\)$/,\n                    getColor: color => color.replace(/,(?!.*,).*(?=\\))|a/g, ''),\n                    getOpacity: color => color.match(/\\.\\d+|[01](?=\\))/)[0]\n                }, {\n                    // hsla\n                    pattern: /^hsla\\(\\d+\\s*,\\s*([\\d.]+%\\s*,\\s*){2}(\\d*(?:\\.\\d+)?)\\)$/,\n                    getColor: color => color.replace(/,(?!.*,).*(?=\\))|a/g, ''),\n                    getOpacity: color => color.match(/\\.\\d+|[01](?=\\))/)[0]\n                }, {\n                    // alphahex\n                    pattern: /^#([0-9A-Fa-f]{4}|[0-9A-Fa-f]{8})$/,\n                    getColor: color => color.length === 5 ? color.substring(0, 4) : color.substring(0, 7),\n                    getOpacity: color => {\n                        if (color.length === 5) {\n                            return (parseInt(color.substring(4, 5) + color.substring(4, 5), 16) / 255).toFixed(2)\n                        } else {\n                            return (parseInt(color.substring(7, 9), 16) / 255).toFixed(2)\n                        }\n                    }\n                }, {\n                    // transparent\n                    pattern: /^transparent$/,\n                    getColor: () => '#fff',\n                    getOpacity: () => '0'\n                }\n            ]\n\n            for (let i = 0; i < patterns.length; i++) {\n                if (patterns[i].pattern.test(inputColor)) {\n                    return {\n                        color: patterns[i].getColor(inputColor),\n                        opacity: patterns[i].getOpacity(inputColor)\n                    }\n                }\n            }\n\n            return {\n                color: inputColor,\n                opacity: '1'\n            }\n        },\n        getColor(color) {\n            return this.parseAlphaColor(color).color\n        },\n        getOpacity(color) {\n            return this.parseAlphaColor(color).opacity\n        }\n    }\n}\n</script>\n\n<style scoped>\n    .vue-star-rating-star {\n        overflow: visible !important;\n    }\n</style>"],"sourceRoot":""}]);
